import React, { Component } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { CircularProgress } from '@material-ui/core';

import Firebase from 'firebase';
import config from '../../config';
import InfoBlock from './InfoBlock';
import RecipesList from './RecipesList';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

function transliterate(word){
    var answer = "", a = {};

   a["Ё"]="YO";a["Й"]="I";a["Ц"]="TS";a["У"]="U";a["К"]="K";a["Е"]="E";a["Н"]="N";a["Г"]="G";a["Ш"]="SH";a["Щ"]="SCH";a["З"]="Z";a["Х"]="H";a["Ъ"]="";
   a["ё"]="yo";a["й"]="i";a["ц"]="ts";a["у"]="u";a["к"]="k";a["е"]="e";a["н"]="n";a["г"]="g";a["ш"]="sh";a["щ"]="sch";a["з"]="z";a["х"]="h";a["ъ"]="";
   a["Ф"]="F";a["Ы"]="I";a["В"]="V";a["А"]="a";a["П"]="P";a["Р"]="R";a["О"]="O";a["Л"]="L";a["Д"]="D";a["Ж"]="ZH";a["Э"]="E";
   a["ф"]="f";a["ы"]="i";a["в"]="v";a["а"]="a";a["п"]="p";a["р"]="r";a["о"]="o";a["л"]="l";a["д"]="d";a["ж"]="zh";a["э"]="e";
   a["Я"]="Ya";a["Ч"]="CH";a["С"]="S";a["М"]="M";a["И"]="I";a["Т"]="T";a["Ь"]="";a["Б"]="B";a["Ю"]="YU";
   a["я"]="ya";a["ч"]="ch";a["с"]="s";a["м"]="m";a["и"]="i";a["т"]="t";a["ь"]="";a["б"]="b";a["ю"]="yu";
   a[" "]="_";

   for (var i in word){
     if (word.hasOwnProperty(i)) {
       if (a[word[i]] === undefined){
         answer += word[i];
       } else {
         answer += a[word[i]];
       }
     }
   }
   return answer;
}

const RecepiesRoute = ({ data }) => { 
    return (
      <div className="container">
        {data && <RecipesList data={data} />}
      </div>
    )
};

class App extends Component {
  
  constructor(props) {
    super(props);
    Firebase.initializeApp(config);

    this.state = {
      loading: true,
      data: null,
      filteredData: null,
    };
  }

  componentDidMount() {
    Firebase.database()
      .ref("/")
      .on("value", snapshot => {
        const data = snapshot.val().data;
        this.setState({ loading: false, data: data.map(item => ({ ...item, id: transliterate(item.title.toLowerCase().replace(' ', '_')) })) });
      });
  }
  render() {
    const { loading, data } = this.state;

    if (loading) {
      return (
        <div className="container" style={{textAlign: 'center'}}>
          <CircularProgress />
        </div>
      );
    }

    return (
      <>
        <CssBaseline />
        <div className="container">
          <InfoBlock/>
          <BrowserRouter basename="/recipes">
            <Switch>
                <Route exact path="/" component={() => <RecepiesRoute data={data} />} />
            </Switch >
          </BrowserRouter>
        </div>
      </>
    );
  }
}

export default App;
